.message {

}

.imageAndText {
    display: flex;
    flex-direction: row-reverse;
    align-items: center;
    gap: 20px;
}

.imageAndText img {
    width: 50px;
    height: 50px;
}

.text {
    max-width: 530px;
    min-height: 60px;
    background-color: #0066CC;
    border-radius: 10px;
    padding: 5px 5px 0 5px;
    position: relative;
}

.text::after {
    content: '';
    position: absolute;
    border-top: 20px solid transparent;
    border-left: 30px solid #0066CC;
    top: 40px;
    right: -22px;
    z-index: -1;

}

.name {
    font-size: 16px;
    font-weight: 600;
    line-height: 20px;
    text-align: right;
    color: white;
}

.messageText {
    font-size: 16px;
    font-weight: 400;
    line-height: 20px;
    color: white;
}

.time {
    text-align: right;
    font-size: 10px;
    font-weight: 600;
    line-height: 13px;
    text-underline-position: from-font;
    text-decoration-skip-ink: none;
    margin-right: 10px;
}