.friendMessage {

}

.friendImageAndText {
    display: flex;
    align-items: center;
    gap: 20px;
}

.friendImageAndText img {
    width: 50px;
    height: 50px;
}

.friendText {
    max-width: 530px;
    min-height: 60px;
    background-color: #F5F7FB;
    border-radius: 10px;
    padding: 5px 5px 0 5px;
    position: relative;
}

.friendText::before {
    content: '';
    position: absolute;
    border-top: 20px solid transparent;
    border-right: 30px solid #F5F7FB;
    top: 40px;
    left: -22px;
}

.friendName {
    font-size: 16px;
    font-weight: 600;
    line-height: 20px;
}

.friendMessageText {
    font-size: 16px;
    font-weight: 400;
    line-height: 20px;
}

.friendTime {
    font-size: 10px;
    font-weight: 600;
    line-height: 13px;
    text-underline-position: from-font;
    text-decoration-skip-ink: none;
    margin-left: 10px;
}
