.sidebar {
    visibility: collapse;
    position: absolute;
    width: 300px;
    height: 100vh;
    border: 1px solid red;
    background-color: #ffffff;
    z-index: 100;
    opacity: 0;

}

.sidebar.open {
    visibility: visible;
    opacity: 1;
    transition: visibility 1s ease;
}

.background {
    background-color: rgba(79, 79, 79, 0.45);
    position: absolute;
    width: 100%;
    height: 100vh;
    z-index: 1;
}

.nav {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 45px;
}

.nav a {
    text-decoration: none;
    color: black;
    font-size: 22px;
    font-weight: 600;
    line-height: 25px;
    text-align: left;
    text-decoration-style: solid;
    text-underline-position: from-font;
    text-decoration-skip-ink: none;

}

.nav a:hover {
    color: #1e70c9;
}

.nav a.active {
    text-decoration: none;
    color: #0059b3;
}
.close {
    transform: translateX(270px);
    border: none;
    background-color: #ffffff;
}

